.article-item {
    display: flex;
    flex-direction: column;
    padding-top: 0.95vh;
    margin-bottom: 5.85vh;
    border-top: 1px solid #6DCEF5;
    border-bottom: 1px solid #6DCEF5;
}

.userid {
    display: flex;
    margin-top: 24px;
    text-align: left;
    padding-bottom: 1.9vh;
    font-size: 1rem;
    margin-left: 4vw;
    margin-right: 4vw;
    justify-content: space-between;
}

.title {
    text-align: left;
    font-size: 1.2rem;
    margin-top: auto;
    margin-bottom: 1.43vh;
    margin-left: 4vw;
    margin-right: 4vw;
}

.title > * {
    font-weight: bold;
    font-size: larger;
    
}

.content {
    /* display: flex; */
    margin-top: 24px;
    text-align: left;
    padding-bottom: 1.9vh;
    font-size: 1rem;
    margin-left: 4vw;
    margin-right: 4vw;
    font-size: large;
}

.content-open {
    max-height: 100%;
    line-height: 30px;
}

.content-close {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    line-height: 30px;
 
}

.icon {
    display: flex;
    margin-top: 2vh;
    margin-bottom: 2vh;
}

.like {
    margin-left: 4vw;
    margin-right: 2vw;
    font-size: xx-large;
}

.comment {
    margin-left: 4vw;
    margin-right: 2vw;
    font-size: xx-large;
}

.item-btn {
    display: flex;
    margin-left: auto;
    margin-right: 4vw;
}

.item-btn > div {
    margin-left: 8px;
}